export default {
	data() {
		return {
			page: 1,
			userList: [],
			/* 窗口尺寸 */
			winSize: {},
			/* 显示遮罩 */
			showShade: false,
			showMask: false,
			/* 显示操作弹窗 */
			showPop: false,
			/* 弹窗按钮列表 */
			popButton: ["标为关注", "置顶聊天", "删除该聊天"],
			popButtonSan: ["发起群聊", "添加朋友", "扫一扫", "收付款", "帮助与反馈"],
			/* 弹窗定位样式 */
			popStyle: "",
			/* 选择的用户下标 */
			pickerUserIndex: -1
		}
	},
	onNavigationBarButtonTap(e) {
		console.log(e, '44')
		this.showMask = true
	},
	onLoad() {
		// this.getListData();
		console.log(this.$store.state.userInfo, 'userInfo')
		this.getWindowSize();







	},
	onShow() {
		this.getListData();
	},
	onReachBottom() {
		let page = this.page
		page++
		this.page=page
		this.$util.get('/wechatUser/findWechatRecordPage?page=' + page).then(res => {
			if (res.data.code == 200) {
				this.userList = [...this.userList, ...res.data.data]
			} else if (res.data.data.length == 0) {
				uni.showToast({
					title: "到底了!",
					icon: 'none'
				})
			} else {
				uni.showToast({
					title: res.data.msg,
					icon: 'none'
				})
			}
		})
	},
	methods: {
		pickerMenuSan(index) {
			if (index == 2) {
				uni.scanCode({
					success: (res) => {
						console.log('条码类型：' + res.scanType + "");
						console.log('条码内容：' + res.result + "");
						console.log('条码字符集：' + res.charSet + "");
						uni.showModal({
							title: '条码类型：' + res.scanType + "",
							content: '条码内容：' + res.result + ""
						})
					},
					fail: function(e) {
						console.log('扫描fail：' + JSON.stringify(e));
					},
					complete: function() {
						console.log('扫描complete');
					}
				})
			} else if (index == 1) {
				uni.navigateTo({
					url: "/pages/index/addNewFriend/addNewFriend",
				})
				this.showMask=false
			}
		},
		/* 获取列表数据 */
		getListData() {
			this.$util.get('/wechatUser/findWechatRecordPage?page=1').then(res => {
				if (res.data.code == 200) {
					this.userList = res.data.data
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})

		},
		/* 获取窗口尺寸 */
		getWindowSize() {
			uni.getSystemInfo({
				success: (res) => {
					this.winSize = {
						"witdh": res.windowWidth,
						"height": res.windowHeight
					}
				}
			})
		},
		/* 长按监听 */
		onLongPress(e) {
			let [touches, style, index] = [e.touches[0], "", e.currentTarget.dataset.index];

			/* 因 非H5端不兼容 style 属性绑定 Object ，所以拼接字符 */
			if (touches.clientY > (this.winSize.height / 2)) {
				style = `bottom:${this.winSize.height-touches.clientY}px;`;
			} else {
				style = `top:${touches.clientY}px;`;
			}
			if (touches.clientX > (this.winSize.witdh / 2)) {
				style += `right:${this.winSize.witdh-touches.clientX}px`;
			} else {
				style += `left:${touches.clientX}px`;
			}

			this.popStyle = style;
			this.pickerUserIndex = Number(index);
			this.showShade = true;
			this.$nextTick(() => {
				setTimeout(() => {
					this.showPop = true;
				}, 100);
			});
		},
		/* 隐藏弹窗 */
		hidePop() {
			this.showPop = false;
			this.showMask = false
			this.pickerUserIndex = -1;
			setTimeout(() => {
				this.showShade = false;
			}, 250);
		},

		/* 选择菜单 */
		pickerMenu(item, index) {
			item = this.userList[this.pickerUserIndex]
			console.log(index, item, `index`);
			// // 在这里开启你的代码秀
			if (index == 2) {
				this.$util.post('/wechatUser/delWechatRecord?wcRecordId=' + item.id).then(res => {
					if (res.data.code == 200) {
						this.getListData()
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none",
						});
					}
				})
			}



			/* 
			 因为隐藏弹窗方法中会将当前选择的用户下标还原为-1,
			 如果行的菜单方法存在异步情况，请在隐藏之前将该值保存，或通过参数传入异步函数中
			 */
			this.hidePop();
		}
	}
}
